.popup__cell {
  height: 100%;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.popup__block {
  display: inline-block;
  text-align: left;
  position: relative;
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
}

.popup_dopclass .popup__block {
  background: #ccc;
  box-shadow: none;
}

.popup_dopclass .popup__block:before {
  position: absolute;
  width: 20px;
  height: 20px;
  background: #ccc;
  transform: rotate(45deg);
  left: -10px;
  top: 21px;
  content: '';
}

.popup__close {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  height: 40px;
  width: 40px;
  cursor: pointer;
  z-index: 2;
}

.popup__close:after,
.popup__close:before {
  position: absolute;
  left: 50%;
  margin: -1px 0 0 -10px;
  top: 50%;
  height: 2px;
  width: 20px;
  background-color: #d5d5d5;
  content: '';
  transform-origin: 50% 50%;
}

.popup__close:before {
  transform: rotate(-45deg);
}

.popup__close:after {
  transform: rotate(45deg);
}

.popup_active .popup__close {
  display: block;
}

.popup__change {
  box-shadow: 0 0 68px rgba(0, 0, 0, 0.17);
  border-radius: 4px;
  background-color: white;
}

.popup__change > div {
  display: block;
}

.popup__body_hidden {
  overflow: hidden;
  -webkit-overflow-scrolling: auto;
  -moz-overflow-scrolling: auto;
  -o-overflow-scrolling: auto;
  -ms-overflow-scrolling: auto;
  overflow-scrolling: auto;
}

